.App {
  /* text-align: center; */
}

.spinning {
  /* height: 40vmin; */
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .spinning {
    animation: spin infinite 0.2s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.parent {
  position: relative;
  top: 0;
  left: 0;
  z-index: -1;
}
.base {
  position: relative;
  top: 0;
  left: 0;
  /* border: 1px red solid; */
}
.wheel {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px green solid;
  /* z-index: 0; */
}
